import { Component } from 'react';
import Alert from '../Alert';

export default class AlertDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const infoProps = {
      type: 'info',
      message: 'info',
      description: 'infoinfoinfoinfoinfoinfoinfoinfoinfoinfoinfo',
    };
    const errorProps = {
      type: 'error',
      message: 'error',
      onClose() { console.log('error'); },
    };
    return (
      <div style={{ width: 400 }}>
        <Alert {...infoProps} showIcon/>
        <Alert {...errorProps} closable/>
        <Alert type='warning' message="" showIcon closable></Alert>
      </div>
    );
  }
}



