import React, { Component } from 'react';
import Menu from '../Menu';
import Icon from '../../icon/Icon';
import style from  './index.css';

export default class MenuDemo extends Component {

  state = {
    theme: 'dark',
    current: '.$m0',
  }

  handleClick = (e) => {
    // const key = (e.key || '').replace(/\.\$/g, '');
    this.setState({
      current: e.key,
    });
    // console.log(key);
  }

  render() {
    return (
      <div>
        <span>1</span>
        <Menu
          // theme={this.state.theme}
          onClick={this.handleClick}
          styleName={'menu'}
          selectedKeys={[this.state.current]}
          type="horizontal-h"
        >

          <Menu.Item key="m0">
            <Icon className="icon" size={14} name={'paper'} />
            
            </Menu.Item>
          <Menu.SubMenu key="m1" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.Item key="m1i1">1</Menu.Item>
            <Menu.Item key="m1i2">2</Menu.Item>
            <Menu.Item key="m1i3">3</Menu.Item>
          </Menu.SubMenu>
          <Menu.SubMenu key="m2" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.Item key="m2g1i1">1</Menu.Item>
            <Menu.Item key="m2g1i2">2</Menu.Item>
            <Menu.Item key="m2g1i3">3</Menu.Item>
            <Menu.Item key="m2g2i1">1</Menu.Item>
            <Menu.Item key="m2g2i2">2</Menu.Item>
            <Menu.Item key="m2g2i3">3</Menu.Item>
          </Menu.SubMenu>
          <Menu.SubMenu key="m3" disabled title={<span><Icon className="icon" size={14} name={'finance'} /><span></span></span>}>
            <Menu.Item key="m3i1">4</Menu.Item>
            <Menu.Item key="m3i2">5</Menu.Item>
          </Menu.SubMenu>
          <Menu.Item key="m4" disabled>
            <Icon className="icon" size={14} name={'paper'} />
            </Menu.Item>
        </Menu>

        <br /><br /><br />

        <span>2</span>
        <Menu
          // theme={this.state.theme}
          onClick={this.handleClick}
          styleName={'menu'}
          selectedKeys={[this.state.current]}
          type="horizontal-v"
        >

          <Menu.Item key="m0">
            <Icon className="icon" size={14} name={'paper'} />
            
            </Menu.Item>
          <Menu.SubMenu key="m1" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.Item key="m1i1">1</Menu.Item>
            <Menu.Item key="m1i2">2</Menu.Item>
            <Menu.Item key="m1i3">3</Menu.Item>
            <Menu.SubMenu key="m1m1" title="">
              <Menu.Item key="m1m1i1">1</Menu.Item>
              <Menu.Item key="m1m1i2">2</Menu.Item>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m2" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.ItemGroup key="m2g1" title="1">
              <Menu.Item key="m2g1i1">1</Menu.Item>
              <Menu.Item key="m2g1i2">2</Menu.Item>
              <Menu.Item key="m2g1i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup key="m2g2" title="1">
              <Menu.Item key="m2g2i1">1</Menu.Item>
              <Menu.Item key="m2g2i2">2</Menu.Item>
              <Menu.Item key="m2g2i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.SubMenu key="m2m2" title="">
              <Menu.ItemGroup key="m2m2g1" title="1">
                <Menu.Item key="m2m2g1i1">1</Menu.Item>
                <Menu.Item key="m2m2g1i2">2</Menu.Item>
              </Menu.ItemGroup>
              <Menu.ItemGroup key="m2m2g2" title="2">
                <Menu.Item key="m2m2g2i1">1</Menu.Item>
                <Menu.Item key="m2m2g2i2">2</Menu.Item>
              </Menu.ItemGroup>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m3" disabled title={<span><Icon className="icon" size={14} name={'finance'} /><span></span></span>}>
            <Menu.Item key="m3i1">4</Menu.Item>
            <Menu.Item key="m3i2">5</Menu.Item>
          </Menu.SubMenu>
          <Menu.Item key="m4" disabled>
            <Icon className="icon" size={14} name={'paper'} />
            </Menu.Item>
        </Menu>

        <br /><br /><br />


        <span>3</span>
        <Menu
          // theme={this.state.theme}
          onClick={this.handleClick}
          style={{ width: 240, height: 500 }}
          styleName={'menu'}
          defaultOpenKeys={['.$m1']}
          selectedKeys={[this.state.current]}
          type="vertical-h"
        >

          <Menu.Item key="m0">
            <Icon className="icon" size={14} name={'paper'} />
            
            </Menu.Item>
          <Menu.SubMenu key="m1" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.Item key="m1i1">1</Menu.Item>
            <Menu.Item key="m1i2">2</Menu.Item>
            <Menu.Item key="m1i3">3</Menu.Item>
            <Menu.SubMenu key="m1m1" title="">
              <Menu.Item key="m1m1i1">1</Menu.Item>
              <Menu.Item key="m1m1i2">2</Menu.Item>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m2" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.ItemGroup key="m2g1" title="1">
              <Menu.Item key="m2g1i1">1</Menu.Item>
              <Menu.Item key="m2g1i2">2</Menu.Item>
              <Menu.Item key="m2g1i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup key="m2g2" title="1">
              <Menu.Item key="m2g2i1">1</Menu.Item>
              <Menu.Item key="m2g2i2">2</Menu.Item>
              <Menu.Item key="m2g2i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.SubMenu key="m2m2" title="">
              <Menu.ItemGroup key="m2m2g1" title="1">
                <Menu.Item key="m2m2g1i1">1</Menu.Item>
                <Menu.Item key="m2m2g1i2">2</Menu.Item>
              </Menu.ItemGroup>
              <Menu.ItemGroup key="m2m2g2" title="2">
                <Menu.Item key="m2m2g2i1">1</Menu.Item>
                <Menu.Item key="m2m2g2i2">2</Menu.Item>
              </Menu.ItemGroup>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m3" disabled title={<span><Icon className="icon" size={14} name={'finance'} /><span></span></span>}>
            <Menu.Item key="m3i1">4</Menu.Item>
            <Menu.Item key="m3i2">5</Menu.Item>
          </Menu.SubMenu>
          <Menu.Item key="m4" disabled>
            <Icon className="icon" size={14} name={'paper'} />
            </Menu.Item>
        </Menu>

        <br /><br /><br />

        <span>4</span>
        <Menu
          // theme={this.state.theme}
          onClick={this.handleClick}
          style={{ width: 240 }}
          styleName={'menu'}
          defaultOpenKeys={['.$m1']}
          selectedKeys={[this.state.current]}
          type="vertical-v"
        >

          <Menu.Item key="m0">
            <Icon className="icon" size={14} name={'paper'} />
            
            </Menu.Item>
          <Menu.SubMenu key="m1" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.Item key="m1i1">1</Menu.Item>
            <Menu.Item key="m1i2">2</Menu.Item>
            <Menu.Item key="m1i3">3</Menu.Item>
            <Menu.SubMenu key="m1m1" title="">
              <Menu.Item key="m1m1i1">1</Menu.Item>
              <Menu.Item key="m1m1i2">2</Menu.Item>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m2" title={<span><Icon className="icon" size={14} name={'paper'} /><span></span></span>}>
            <Menu.ItemGroup key="m2g1" title="1">
              <Menu.Item key="m2g1i1">1</Menu.Item>
              <Menu.Item key="m2g1i2">2</Menu.Item>
              <Menu.Item key="m2g1i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup key="m2g2" title="1">
              <Menu.Item key="m2g2i1">1</Menu.Item>
              <Menu.Item key="m2g2i2">2</Menu.Item>
              <Menu.Item key="m2g2i3">3</Menu.Item>
            </Menu.ItemGroup>
            <Menu.SubMenu key="m2m2" title="">
              <Menu.ItemGroup key="m2m2g1" title="1">
                <Menu.Item key="m2m2g1i1">1</Menu.Item>
                <Menu.Item key="m2m2g1i2">2</Menu.Item>
              </Menu.ItemGroup>
              <Menu.ItemGroup key="m2m2g2" title="2">
                <Menu.Item key="m2m2g2i1">1</Menu.Item>
                <Menu.Item key="m2m2g2i2">2</Menu.Item>
              </Menu.ItemGroup>
            </Menu.SubMenu>
          </Menu.SubMenu>
          <Menu.SubMenu key="m3" disabled title={<span><Icon className="icon" size={14} name={'finance'} /><span></span></span>}>
            <Menu.Item key="m3i1">4</Menu.Item>
            <Menu.Item key="m3i2">5</Menu.Item>
          </Menu.SubMenu>
          <Menu.Item key="m4" disabled>
            <Icon className="icon" size={14} name={'paper'} />
            </Menu.Item>
        </Menu>

      </div>
    );
  }
}



